:host .login-flyout__divider {
  border-color: var(--mdx-comp-login-flyout-color-light-container-divider-border-color-default);
  border-width: var(--mdx-comp-login-flyout-border-width-desktop-container-divider-default);
  border-style: solid;
  margin: var(--mdx-comp-login-flyout-spacing-desktop-container-divider-margin-top) 0
    var(--mdx-comp-login-flyout-spacing-desktop-container-divider-margin-bottom) 0;

  @media screen and (max-width: 768px) {
    margin: var(--mdx-comp-login-flyout-spacing-mobile-container-divider-margin-top) 0
      var(--mdx-comp-login-flyout-spacing-mobile-container-divider-margin-bottom) 0;
  }
}

:host([dark]) .login-flyout__divider {
  border-color: var(--mdx-comp-login-flyout-color-dark-container-divider-border-color-default);
}
